@font-face {
	font-family: 'FontAwesome';
	src: url('/assets/fonts/fontawesome/fontawesome-webfont.eot?v=4.7.0');
	src: url('/assets/fonts/fontawesome/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('/assets/fonts/fontawesome/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('/assets/fonts/fontawesome/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('/assets/fonts/fontawesome/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('/assets/fonts/fontawesome/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
	font-weight: normal;
	font-style: normal;
}

*{
	box-sizing : border-box;
}

HTML{
	font-size : 65%;
}

BODY{
	background-image : url('/images/bg.jpg');
	background-size : cover;
	background-position : bottom;
}

.u-linklabel{
	display : inline;
}

.c-main_menu{
	position: fixed;
	bottom: 0px;
	left: 50%;
	width: 0px;
	height: 0px;
	display: block;
	transform-style: preserve-3d;
	perspective: 500px;
	perspective-origin: bottom;
	
	&__panel{
		position: absolute;
		width: 220px;
		height: 220px;
		border-radius: 20px;
		box-shadow: inset 0px 0px 12px 8px rgba(255,255,255,0.4);
		background-color: rgba(97, 183, 255, 0.4);
		transition : all 500ms ease-in-out;
		
		&.__order_1{ top :   0px; left :   0px; }
		&.__order_2{ top :   0px; left : 240px; }
		&.__order_3{ top :   0px; left : 480px; }
		&.__order_4{ top : 240px; left :   0px; }
		&.__order_5{ top : 240px; left : 240px; }
		&.__order_6{ top : 240px; left : 480px; }
		&.__order_7{ top : 480px; left :   0px; }
		&.__order_8{ top : 480px; left : 240px; }
		&.__order_9{ top : 480px; left : 480px; }
		
		&::before{
			content: attr(data-icon);
			font-family: 'FontAwesome';
			display : block;
			width : 100%;
			margin-top : 30px;
			height : 150px;
			font-size : 12rem;
			text-align : center;
			text-shadow: -1px -1px 1px rgba(13, 55, 98, 0.8),1px 1px 1px rgba(255,255,255,0.7);
			pointer-events : none;
		}
		
		& >A{
			position : absolute;
			width : 100%;
			height : 100%;
			left : 0px;
			bottom : 0px;
			font-size : 2.5rem;
			font-family: 'Nunito';
			text-decoration: none;
			text-align : center;
			text-shadow: -1px -1px 0px rgba(13,55,98,1);
			
			& > .u-linklabel{
				position : absolute;
				width : 100%;
				height : auto;
				left : 0px;
				bottom : 20px;
				display : inline-block;
			}
		}
		
		& >A,
		&::before{
			opacity : 0.5;
			color: rgb(255, 255, 255);
			transition : color 500ms ease-in-out;
		}
		
		&.__main{
			background-color: rgba(255, 255, 255, 0.4);
			box-shadow: inset 0px 0px 12px 8px rgba(124, 186, 231, 0.25);
			& > A,
			&::before{
				color : rgb(118, 168, 247);
				text-shadow: none;
			}
		}
	}
	
	&__frame{
		position: relative;
		top: -750px;
		left: -350px;
		width: 700px;
		height: 700px;
		transform: rotateX(40deg);
		transform-origin: bottom;
		list-style: none;
		margin: 0px;
		padding: 0px;
		transition : transform 500ms ease-in-out;
		
	}
	&__frame.is-active{
		transform: rotateX(0deg);
	}
	&__frame.is-active > &__panel{
		background-color: rgba(34, 96, 214, 0.7);
		
		&.__main{
			background-color: rgba(255, 255, 255, 0.7);
			box-shadow: inset 0px 0px 12px 8px rgba(73, 133, 225, 0.25);
			& > A,
			&::before{
				color: rgb(10, 79, 187);
			}
		}
	}
}
















